.title-container {
    position: relative;
    padding-top: 5vw;
    padding-bottom: 13.5vw;
    text-align: center;
    width: auto;
    height: auto;
    background-color: rgb(0, 31, 44);
    z-index: 100
}

h2 {
    position: relative;
    font-size: 7vw;
    letter-spacing: .4vw;
    color: rgb(239, 234, 237);
}

h2 > span {
    color: rgb(02, 143, 235);
}

h3 {
    margin-left: 10vw;
    margin-right: 10vw;
    margin-top: -2vw;
    font-weight: 400;
    font-size: 3.5vw;
    line-height: 6vw;
    letter-spacing: .4vw;
}

.underline {
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    margin-top: 5vw;
    width: 18vw;
    height: 1.2vw;
    background-color: white;
    border-radius: 50%;
}

.paragraph {
    position: relative;
    width: auto;
    height: auto;
    margin-top: 15vw;
    margin-left: 6vw;
    margin-right: 6vw;
    font-weight: 100;
    font-size: 3.5vw;
    line-height: 6vw;
    text-align: justify;
    letter-spacing: .4vw;
}