.portfolio__book {
    position: relative;
    padding-top: 20vw;
    padding-left: 10vw;
    padding-right: 10vw;
    padding-bottom: 10vw;
    width: auto;
    height: auto;
    background-color: rgb(234, 240, 245);
}

.portfolio__book__link {
    position: relative;
    padding-top: 35%;
    text-align: center;
    margin-left: 0;
    margin-bottom: 10vw;
    list-style: none;
    width: auto;
    height: 70vw;
    border-radius: 4vw;
    background-color: white;
    color: black;
    font-size: 7vw;
    font-weight: bold;
    box-shadow: 0px 10px 15px -3px rgba(0,0,0,0.1),0px 10px 15px -3px rgba(0,0,0,0.1);
}