.footer {
	position: relative;
    width: auto;
	height: auto;
    text-align: center;
	background-color: black;
}

.footer__fellow-me {
    margin-top: 10vw;
    margin-bottom: 20vw;
}

.footer__link {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
    top: -10vw;
    left: 50%;
    transform: translate(-50%, 0);
    max-width: 58vw;
    width: auto;
    text-align: center;
    align-items: center;
}

.footer__link__icon {
	position: relative;
    color: white;
    font-size: 6.8vw;
    cursor: pointer;
    transition: font-size .2s, color .2s;
}

.footer__link__icon:hover {
	color: rgb(02, 143, 235);
    font-size: 15vw;
    transition: font-size .2s, color .2s;
}

.footer__logo {
	position: relative;
    margin-top: 10vw;;
    margin-bottom: 5vw;
	top: 4.5vw;
    font-family: 'Open Sans', sans-serif;
	font-size: 8vw;
	font-weight: 800;
	letter-spacing: .2vw;
	color: rgb(87, 87, 87);
}

.footer__logo-fr {
	font-size: 5vw;
}

.footer__website {
    position: relative;
    font-family: 'Open Sans', sans-serif;
	font-size: 3.5vw;
    font-weight: 100;
    color: white;
    text-decoration: none;
}

.footer__copyright {
    position: relative;
    margin-top: 20vw;
    margin-bottom: 0;
    bottom: 0;
    padding-top: 2.5vw;
    padding-bottom: 2.5vw;
    width: auto;
    height: auto;
    font-size: 3.2vw;
    background-color: rgb(25, 25, 25);
}