.navbar {
	position: relative;
    width: auto;
	background: linear-gradient(to top, rgb(75, 75, 75), 20%, transparent);
	overflow: hidden;
	transition: background-color 2s;
}

.navbar-closed {
	height: 39.2vw;
	background-color: black;
	transition: height .3s, background-color .8s;
}

.navbar-open {
	height: 135vw;
	background-color: rgba(56, 56, 56, 0.25);
	transition: height .4s, background-color 1.5s;
}

.navbar__logo {
	position: relative;
	top: 4.5vw;
	margin-left: 4.5vw;
	font-family: 'Open Sans', sans-serif;
	font-size: 9vw;
	font-weight: 800;
	letter-spacing: .2vw;
	color: rgb(2, 143, 235);
}

.navbar__logo-fr {
	font-size: 5vw;
	color: rgb(229, 234, 237);
}

.navbar__button__burger {
	position: relative;
    float: right;
    margin-top: 15vw;
	margin-right: 7.73vw;
	width: 8.26vw;
	height: auto;
	cursor: pointer;
}

menu {
	position: relative;
	display: flex;
	flex-direction: column;
	left: -4.5vw;
	top: 15vw;
	width: 87vw;
	overflow: hidden;
}

.navbar__menu-open {
	height: 80vw;
	transition-delay: .2s;
	transition-duration: .25s;
}

.navbar__menu-closed {
	height: 0;
	transition-duration: .1s;
}

.nav__menu__link, a:link, a:visited, a:hover {
	text-decoration: none !important;
}

.nav__menu__link {
	position: relative;
	line-height: 10vw;
	letter-spacing: .35vw;
	border-bottom: .5px solid rgba(107, 177, 224, .2);
    padding-bottom: .5vw;
	cursor: pointer;
	letter-spacing: .3vw;
	font-size: 4vw;
	font-weight: 300;
	color: rgb(229, 234, 237);
	transition: all .3s;
}

.nav__menu__link:hover {
	border-bottom: .2px solid rgb(255, 255, 255);
	text-decoration: none;
	padding-left: 5vw;
	font-size: 5.2vw;
	font-weight: 200;
	color: white;
	transition: all .2s;
}