.about__arrow-down {
    position: absolute;
    top: 137vw;
    left: 50%;
    transform: translate(-50%, 0);
    width: 7vw;
    height: auto;
    opacity: .3;
	animation: bounce 0.38s ease infinite alternate; 
    z-index: 300;
} 

@keyframes bounce { 
    from {transform: translateY(0);} to {transform: translateY(-3vw);} 
}

.about__article {
    position: relative;
    width: 100%;
    height: auto;
    padding-bottom: 45vw;
}

.about__article__picture {
    position: relative;
    width: 100vw;
    height: auto;
    filter: brightness(40%);
}

.about__article__ref {
    position: relative;
    text-align: center;
    margin-top: -25.3vw;
    margin-bottom: -45vw;
    padding-top: 2vw;
    padding-left: 6vw;
    padding-right: 6vw;
    top: 0;
    width: 100%;
    min-height: 74vw;
    height: auto;
    background-color: rgb(188, 245, 235);
}

.about__article__ref__title {
    position: relative;
    font-size: 5vw;
    font-weight: 500;
    letter-spacing: 1.5vw;
    color: rgb(22, 99, 73);
}

.about__article__regard {
    position: relative;
    top: -118vw;
    right: 6vw;
    font-weight: 100;
    font-size: 3.5vw;
    line-height: 6vw;
    text-align: right;
    letter-spacing: .4vw;
}

.about__article__menu {
    position: relative;
    padding-left: 0;
    padding-right: 0;
    left: 50%;
    transform: translate(-50%, 0);
    display: flex;
    flex-direction: row;
    justify-content: center; 
    width: 100%;
    height: 15.5vw;
    z-index: 200;
}

.about__article__menu--open {
    top: -138vw;
}

.about__article__menu--closed {
    top: -21vw;
}

.about__article__menu > a {
    color: rgb(239, 234, 237);
    letter-spacing: .2vw;
}

.about__article__menu__title--open {
    text-decoration: none;
    margin-top: 6vw;
    font-size: 5vw;
    font-weight: 300;
    cursor: default;
}

.about__article__menu__title--closed {
    text-decoration: none;
    padding-top: 5.5vw;
    text-align: center;
    width: 34vw;
    font-size: 3.8vw;
    font-weight: 400;
    cursor: pointer;
}

.about__article__menu__title--closed-history {
    background-color: rgb(13, 63, 45);
}

.about__article__menu__title--closed-values {
    background-color: rgb(22, 99, 73);
}

.about__article__menu__title--closed-cv {
    background-color: rgb(29, 131, 97);
}

.about__article__collapse {
    position: absolute;
    top: 0;
    width: 100%;
    height: 210vw;
}

.about__article__collapse__history {
    background-color: rgb(13, 63, 45);
}

.about__article__collapse__values {
    background-color: rgb(22, 99, 73);
}

.about__article__collapse__cv {
    background-color: rgb(29, 131, 97);
}

.about__article__collapse--open {
    display: block;
}

.about__article__collapse--closed {
    display: none;
}

.about__article__collapse__history__text {
    position: relative;
    text-align: justify;
    width: auto;
    top: 20vw;
    margin-left: 6vw;
    margin-right: 6vw;
    letter-spacing: .2vw;
    font-size: 3.5vw;
    font-weight: 100;
}

.about__article__collapse__history__text__pic {
    position: relative;
    width: 100%;
    height: auto;
    margin-top: -3vw;
    margin-bottom: 12vw;
    border-radius: 2.5vw;
}

.arrow-back {
    position: absolute;
    top: 4vw;
    margin-left: 6vw;
    font-size: 4vw;
    color: rgb(229, 234, 237);
    transition: .2s;
    cursor: pointer;
    z-index: 500;
}

.arrow-back:hover {
    font-size: 5.5vw;
    color: rgb(94, 245, 175);
    transition: .2s;
}

.arrow-back--open {
    display: block;
}

.arrow-back--closed {
    display: none;
}