.background {
	position: relative;
    width: auto;
    height: auto;
    overflow: hidden;
    z-index: -1;
}

.keyboard {
    position: fixed;
    left: 50%;
    transform: translate(-50%, 0);
    top: 0;
    width: 200vw;
    height: auto;
    filter: blur(3px);
    filter: brightness(120%);
}