.banner {
    position: relative;
    width: 100%;
    height: 145vw;
    background-color: black;
}

.banner__title{
    position: relative;
    top: 5vw;
    margin-left: 5vw;
	font-family: 'Play', sans-serif;
	font-weight: bold;
	font-size: 11.5vw;
	color: white;
    margin-bottom: 20vw;
    line-height: 12vw;
    letter-spacing: .52vw;
}

.banner__title > span {
	font-family: 'Open Sans', sans-serif;
	font-size: 19.8vw;
	color: rgba(2, 143, 235);
	font-weight: 700;
}

.banner__subtitle {
    position: relative;
    top: -10vw;
    margin-left: 6.5vw;
    font-size: 8vw;
    font-weight: 200;
    color: rgb(107, 177, 224);
}

.banner__text {
    top: -15vw;
    position: relative;
    margin-left: 6.5vw;
    margin-bottom: 15vw;
    font-size: 3.1vw;
    font-weight: 200;
    line-height: 5vw;
    letter-spacing: .52vw;
}

.banner__text > span {
    font-family: 'Open Sans', sans-serif;
    font-size: 3.2vw;
    font-weight: bold;
    color: rgb(239, 234, 237);
}

.banner__button {
    position: absolute;
    margin-top: -10vw;
    left: 50%;
    transform: translate(-50%, 0);
    margin-bottom: 28.730vw;
    font-size: 15vw;
    color: rgb(41, 235, 2);
    /*color: rgb(155, 195, 221);*/
    cursor: pointer;
    transition: all .2s;
    z-index: 100;
}

.banner__button:hover {
    font-size: 18vw;
    transition: all .2s;
}

.banner__button:active {
    color: white;
}

.banner__picture {
    position: relative;
    top: -121vw;
    margin-left: -70vw;
    width: 230vw;
    height: auto;
    margin-bottom: -2vw;
    opacity: .2;
}